.user-avatar-pip {
  position: fixed;
  background: rgba(20, 20, 30, 0.95);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  overflow: hidden;
  transition: all 0.3s ease;
  z-index: 1000;
  cursor: move;
  resize: both;
  min-width: 200px;
  min-height: 250px;
  max-width: 500px;
  max-height: 600px;
}

/* Position variants */
.user-avatar-pip.top-left {
  top: 20px;
  left: 20px;
}

.user-avatar-pip.top-right {
  top: 20px;
  right: 20px;
}

.user-avatar-pip.bottom-left {
  bottom: 20px;
  left: 20px;
}

.user-avatar-pip.bottom-right {
  bottom: 20px;
  right: 20px;
}

/* Size variants */
.user-avatar-pip.pip-small {
  width: 200px;
  height: 250px;
}

.user-avatar-pip.pip-medium {
  width: 280px;
  height: 350px;
}

.user-avatar-pip.pip-large {
  width: 360px;
  height: 450px;
}

/* Minimized state */
.user-avatar-pip.minimized {
  height: auto;
}

.user-avatar-pip.minimized .pip-content {
  display: none;
}

/* Header */
.pip-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.pip-status {
  display: flex;
  align-items: center;
  gap: 8px;
}

.status-indicator {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #666;
  transition: background 0.3s;
}

.status-indicator.active {
  background: #4ade80;
  box-shadow: 0 0 8px rgba(74, 222, 128, 0.5);
}

.status-text {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
}

.pip-controls {
  display: flex;
  gap: 4px;
}

.pip-button {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  padding: 4px 8px;
  font-size: 14px;
  border-radius: 4px;
  transition: all 0.2s;
}

.pip-button:hover {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.9);
}

/* Content */
.pip-content {
  position: relative;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2a2a3e 0%, #16213e 100%);
  border-radius: 0 0 12px 12px;
  overflow: hidden;
}

.pip-content canvas {
  width: 100% !important;
  height: 100% !important;
}

/* Error state */
.pip-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 20px;
  text-align: center;
}

.pip-error p {
  color: #ef4444;
  margin-bottom: 16px;
  font-size: 14px;
}

.pip-error button {
  background: #7c3aed;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  transition: background 0.2s;
}

.pip-error button:hover {
  background: #6d28d9;
}

/* Expression debug */
.expression-debug {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  padding: 8px;
  backdrop-filter: blur(8px);
}

.expression-mini {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.expression-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 11px;
}

.expression-name {
  color: rgba(255, 255, 255, 0.7);
  width: 80px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.expression-bar {
  flex: 1;
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
}

.expression-fill {
  height: 100%;
  background: linear-gradient(to right, #7c3aed, #a855f7);
  transition: width 0.2s ease;
}

/* Draggable support (optional) */
.user-avatar-pip.dragging {
  cursor: move;
  opacity: 0.8;
}

/* Mobile responsive */
@media (max-width: 768px) {
  .user-avatar-pip.pip-large {
    width: 280px;
    height: 350px;
  }
  
  .user-avatar-pip.pip-medium {
    width: 200px;
    height: 250px;
  }
}
