/* XR Cupid Risograph Design System */
/* Inspired by vintage Risograph prints with playful, vibrant colors and handmade aesthetics */

:root {
  /* Primary Risograph-inspired color palette */
  --riso-pink: #FF6B9D;      /* Warm pink/coral */
  --riso-pink-light: #FFB3CA; /* Light pink for overlays */
  --riso-teal: #4ECDC4;      /* Soft teal */
  --riso-teal-light: #8FE0DA; /* Light teal variant */
  --riso-goldenrod: #FFD93D; /* Warm yellow */
  --riso-goldenrod-light: #FFE88E; /* Light goldenrod */
  --riso-indigo: #6C5CE7;    /* Soft purple/indigo */
  --riso-indigo-light: #A29BFE; /* Light indigo */
  
  /* Overlay transparencies for layering effect */
  --overlay-pink: rgba(255, 107, 157, 0.15);
  --overlay-teal: rgba(78, 205, 196, 0.15);
  --overlay-goldenrod: rgba(255, 217, 61, 0.15);
  --overlay-indigo: rgba(108, 92, 231, 0.15);
  
  /* Background and neutral colors */
  --bg-cream: #FFF9F0;             /* Soft paper-like background */
  --bg-light: #FFFCF8;             /* Lighter variant */
  --text-dark: #2D2A26;            /* Warm dark text */
  --text-medium: #5A5651;          /* Medium text */
  --text-light: #8B8580;           /* Light text */
  
  /* Typography - Playful but readable */
  --font-display: 'Fredoka', 'Comic Neue', system-ui, sans-serif;
  --font-body: 'Work Sans', 'Nunito', -apple-system, sans-serif;
  --font-mono: 'Space Mono', 'Courier New', monospace;
  
  /* Spacing system */
  --space-xs: 4px;
  --space-sm: 8px;
  --space-md: 16px;
  --space-lg: 24px;
  --space-xl: 32px;
  --space-2xl: 48px;
  --space-3xl: 64px;
  
  /* Font sizes */
  --font-size-xs: 0.75rem;     /* 12px */
  --font-size-sm: 0.875rem;    /* 14px */
  --font-size-base: 1rem;      /* 16px */
  --font-size-lg: 1.125rem;    /* 18px */
  --font-size-xl: 1.25rem;     /* 20px */
  --font-size-h4: 1.5rem;      /* 24px */
  --font-size-h3: 1.875rem;    /* 30px */
  --font-size-h2: 2.25rem;     /* 36px */
  --font-size-h1: 3rem;        /* 48px */
  
  /* Font weights */
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-semi-bold: 600;
  --font-weight-bold: 700;
  
  /* Line heights */
  --line-height-tight: 1.2;
  --line-height-heading: 1.3;
  --line-height-base: 1.6;
  --line-height-relaxed: 1.8;
  
  /* Opacity values */
  --opacity-low: 0.25;
  --opacity-medium: 0.7;
  --opacity-high: 0.9;
  --opacity-full: 1;
  
  /* Border radius for rounded, friendly feel */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 16px;
  --radius-xl: 24px;
  --radius-full: 9999px;
  
  /* Shadows with offset for Risograph effect */
  --shadow-sm: 2px 2px 0 var(--riso-pink);
  --shadow-md: 4px 4px 0 var(--riso-teal);
  --shadow-lg: 6px 6px 0 var(--riso-indigo);
  --shadow-print: 3px 3px 0 rgba(45, 42, 38, 0.15);
  
  /* Animation timings */
  --transition-fast: 150ms ease-out;
  --transition-medium: 300ms ease-out;
  --transition-slow: 500ms ease-out;
}

/* Global Resets with Risograph aesthetic */
* {
  box-sizing: border-box;
}

body {
  background-color: var(--bg-cream);
  color: var(--text-dark);
  font-family: var(--font-body);
  font-size: 16px;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  
  /* Subtle paper texture background */
  background-image: 
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 35px,
      var(--overlay-pink) 35px,
      var(--overlay-pink) 70px
    ),
    repeating-linear-gradient(
      -45deg,
      transparent,
      transparent 35px,
      var(--overlay-teal) 35px,
      var(--overlay-teal) 70px
    );
  background-blend-mode: multiply;
}

/* Typography with Risograph style */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-display);
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 var(--space-md) 0;
  color: var(--text-dark);
}

h1 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  letter-spacing: -0.02em;
  
  /* Offset print effect */
  position: relative;
}

h1::before {
  content: attr(data-text);
  position: absolute;
  left: 2px;
  top: 2px;
  color: var(--riso-pink);
  opacity: 0.7;
  z-index: -1;
  pointer-events: none;
}

h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  letter-spacing: -0.01em;
}

h3 {
  font-size: clamp(1.5rem, 3vw, 1.875rem);
}

p {
  margin: 0 0 var(--space-md) 0;
  font-weight: 400;
}

/* Risograph-style buttons */
.riso-button {
  font-family: var(--font-display);
  font-weight: 600;
  font-size: 1rem;
  padding: var(--space-md) var(--space-lg);
  border: 3px solid var(--text-dark);
  background-color: var(--riso-pink);
  color: var(--text-dark);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all var(--transition-fast);
  position: relative;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  
  /* Offset shadow for depth */
  box-shadow: 4px 4px 0 var(--text-dark);
}

.riso-button:hover {
  transform: translate(-2px, -2px);
  box-shadow: 6px 6px 0 var(--text-dark);
  background-color: var(--riso-pink-light);
}

.riso-button:active {
  transform: translate(2px, 2px);
  box-shadow: 2px 2px 0 var(--text-dark);
}

.riso-button.secondary {
  background-color: var(--riso-teal);
}

.riso-button.secondary:hover {
  background-color: var(--riso-teal-light);
}

.riso-button.tertiary {
  background-color: var(--riso-goldenrod);
}

.riso-button.tertiary:hover {
  background-color: var(--riso-goldenrod-light);
}

/* Card component with paper texture */
.riso-card {
  background-color: var(--bg-light);
  border: 3px solid var(--text-dark);
  border-radius: var(--radius-lg);
  padding: var(--space-xl);
  position: relative;
  box-shadow: 5px 5px 0 var(--text-dark);
  
  /* Texture overlay */
  background-image: 
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent 1px,
      var(--overlay-pink) 1px,
      var(--overlay-pink) 2px
    );
  background-size: 4px 4px;
}

/* Halftone pattern overlay */
.halftone-overlay {
  position: relative;
  overflow: hidden;
}

.halftone-overlay::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(circle, var(--text-dark) 1px, transparent 1px);
  background-size: 8px 8px;
  opacity: 0.05;
  pointer-events: none;
}

/* Registration marks for authentic print feel */
.registration-mark {
  position: absolute;
  width: 20px;
  height: 20px;
  border: 2px solid var(--text-dark);
  border-radius: 50%;
}

.registration-mark::before,
.registration-mark::after {
  content: '';
  position: absolute;
  background-color: var(--text-dark);
}

.registration-mark::before {
  width: 20px;
  height: 2px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.registration-mark::after {
  width: 2px;
  height: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Angel illustration container */
.angel-hero {
  position: relative;
  padding: var(--space-3xl) var(--space-xl);
  text-align: center;
  overflow: hidden;
  
  /* Layered background for depth */
  background: 
    radial-gradient(ellipse at center top, var(--overlay-pink) 0%, transparent 50%),
    radial-gradient(ellipse at center bottom, var(--overlay-teal) 0%, transparent 50%),
    var(--bg-cream);
}

/* Text with offset print effect */
.offset-text {
  position: relative;
  display: inline-block;
  font-family: var(--font-display);
  font-weight: 700;
  font-size: clamp(3rem, 8vw, 5rem);
  letter-spacing: -0.03em;
  color: var(--text-dark);
}

.offset-text::before,
.offset-text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.offset-text::before {
  color: var(--riso-pink);
  transform: translate(3px, 3px);
  z-index: -2;
}

.offset-text::after {
  color: var(--riso-teal);
  transform: translate(-3px, -3px);
  z-index: -1;
  mix-blend-mode: multiply;
}

/* Playful hover effects */
.hover-wiggle {
  transition: transform var(--transition-fast);
}

.hover-wiggle:hover {
  animation: wiggle 0.5s ease-in-out;
}

@keyframes wiggle {
  0%, 100% { transform: rotate(0deg); }
  25% { transform: rotate(-3deg); }
  75% { transform: rotate(3deg); }
}

/* Heart shape decoration */
.heart-decoration {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 36px;
  transform: rotate(-45deg);
  background-color: var(--riso-pink);
  margin: 0 var(--space-sm);
}

.heart-decoration::before,
.heart-decoration::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--riso-pink);
}

.heart-decoration::before {
  top: -20px;
  left: 0;
}

.heart-decoration::after {
  left: 20px;
  top: 0;
}

/* Gradient overlays for sections */
.gradient-overlay {
  background: linear-gradient(
    135deg,
    var(--overlay-pink) 0%,
    var(--overlay-teal) 25%,
    var(--overlay-goldenrod) 50%,
    var(--overlay-indigo) 75%,
    var(--overlay-pink) 100%
  );
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

/* Loading animation with Risograph style */
.riso-loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.riso-loader div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: var(--riso-pink);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.riso-loader div:nth-child(1) {
  left: 8px;
  animation: riso-loader1 0.6s infinite;
  background: var(--riso-pink);
}

.riso-loader div:nth-child(2) {
  left: 8px;
  animation: riso-loader2 0.6s infinite;
  background: var(--riso-teal);
}

.riso-loader div:nth-child(3) {
  left: 32px;
  animation: riso-loader2 0.6s infinite;
  background: var(--riso-goldenrod);
}

.riso-loader div:nth-child(4) {
  left: 56px;
  animation: riso-loader3 0.6s infinite;
  background: var(--riso-indigo);
}

@keyframes riso-loader1 {
  0% { transform: scale(0); }
  100% { transform: scale(1); }
}

@keyframes riso-loader3 {
  0% { transform: scale(1); }
  100% { transform: scale(0); }
}

@keyframes riso-loader2 {
  0% { transform: translate(0, 0); }
  100% { transform: translate(24px, 0); }
}

/* Utility classes */
.text-pink { color: var(--riso-pink); }
.text-teal { color: var(--riso-teal); }
.text-goldenrod { color: var(--riso-goldenrod); }
.text-indigo { color: var(--riso-indigo); }

.bg-pink { background-color: var(--riso-pink); }
.bg-teal { background-color: var(--riso-teal); }
.bg-goldenrod { background-color: var(--riso-goldenrod); }
.bg-indigo { background-color: var(--riso-indigo); }

.border-offset {
  border: 3px solid var(--text-dark);
  box-shadow: 3px 3px 0 var(--riso-pink);
}

.riso-overlay {
  position: relative;
}

.riso-overlay::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 10px,
      var(--overlay-pink) 10px,
      var(--overlay-pink) 20px
    );
  opacity: 0.1;
  pointer-events: none;
}

.offset-text {
  position: relative;
  display: inline-block;
  font-weight: 900;
}

.offset-text::before,
.offset-text::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.7;
}

.offset-text::before {
  color: var(--riso-pink);
  transform: translate(2px, 2px);
  z-index: -1;
}

.offset-text::after {
  color: var(--riso-teal);
  transform: translate(4px, 4px);
  z-index: -2;
}

.riso-button {
  background-color: var(--riso-pink);
  color: var(--bg-light);
  border: 3px solid var(--text-dark);
  padding: var(--space-md) var(--space-xl);
  font-weight: var(--font-weight-bold);
  font-family: var(--font-body);
  cursor: pointer;
  transition: all var(--transition-fast);
  border-radius: var(--radius-full);
  position: relative;
  box-shadow: 4px 4px 0 var(--text-dark);
  text-transform: none;
  letter-spacing: 0.01em;
}

.riso-button:hover {
  transform: translate(-2px, -2px);
  box-shadow: 6px 6px 0 var(--text-dark);
  background-color: var(--riso-pink-light);
}

.riso-button:active {
  transform: translate(0, 0);
  box-shadow: 2px 2px 0 var(--text-dark);
}

.riso-button.secondary {
  background-color: var(--bg-light);
  color: var(--text-dark);
  border: 2px solid var(--text-dark);
  box-shadow: 3px 3px 0 var(--riso-teal);
}

.riso-button.secondary:hover {
  background-color: var(--overlay-teal);
  box-shadow: 5px 5px 0 var(--riso-teal);
}

.riso-button.goldenrod {
  background-color: var(--riso-goldenrod);
  color: var(--text-dark);
  box-shadow: 4px 4px 0 var(--riso-indigo);
}

.riso-button.goldenrod:hover {
  background-color: var(--riso-goldenrod-light);
  box-shadow: 6px 6px 0 var(--riso-indigo);
}

.riso-card {
  background-color: var(--bg-light);
  border: 3px solid var(--text-dark);
  border-radius: var(--radius-lg);
  padding: var(--space-xl);
  position: relative;
  overflow: hidden;
  transition: all var(--transition-medium);
  box-shadow: 5px 5px 0 var(--riso-indigo);
}

.riso-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    repeating-linear-gradient(
      45deg,
      transparent,
      transparent 20px,
      var(--overlay-teal) 20px,
      var(--overlay-teal) 40px
    );
  opacity: 0.3;
  pointer-events: none;
}

.riso-card:hover {
  transform: translate(-3px, -3px);
  box-shadow: 8px 8px 0 var(--riso-indigo);
}

/* Mobile responsive adjustments */
@media (max-width: 768px) {
  :root {
    --space-md: 12px;
    --space-lg: 18px;
    --space-xl: 24px;
    --space-2xl: 36px;
    --space-3xl: 48px;
  }
  
  .riso-card {
    padding: var(--space-lg);
  }
}
