.practice-date {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #0a0a0f;
  color: #fff;
  overflow: hidden;
}

.date-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.date-info h2 {
  margin: 0;
  font-size: 1.5rem;
}

.date-info p {
  margin: 0;
  opacity: 0.7;
  font-size: 0.9rem;
}

.date-content {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 450px;
  gap: 2rem;
  padding: 2rem;
  overflow: hidden;
}

.date-scene {
  background: linear-gradient(135deg, rgba(236, 72, 153, 0.05) 0%, rgba(168, 85, 247, 0.05) 100%);
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.date-canvas {
  width: 100%;
  height: 100%;
}

.date-interface {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  overflow-y: auto;
  padding-right: 1rem;
}

.date-interface::-webkit-scrollbar {
  width: 8px;
}

.date-interface::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.date-interface::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}

.date-metrics {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 1.5rem;
  backdrop-filter: blur(10px);
}

.date-metrics h3 {
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
}

.metric-bars {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.metric {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.metric span {
  width: 100px;
  font-size: 0.9rem;
  opacity: 0.8;
}

.metric-bar {
  flex: 1;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
}

.metric-fill {
  height: 100%;
  background: linear-gradient(90deg, #667eea 0%, #a855f7 100%);
  transition: width 0.5s ease;
  border-radius: 4px;
}

.metric-fill.attraction {
  background: linear-gradient(90deg, #ec4899 0%, #f472b6 100%);
}

.metric-fill.comfort {
  background: linear-gradient(90deg, #10b981 0%, #34d399 100%);
}

.metric-fill.engagement {
  background: linear-gradient(90deg, #f59e0b 0%, #fbbf24 100%);
}

.conversation-prompts {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 1.5rem;
}

.conversation-prompts h3 {
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
}

.prompts-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
}

.prompt-button {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.25rem;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
}

.prompt-button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-1px);
}

.prompt-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.prompt-button.used {
  background: rgba(102, 126, 234, 0.1);
  border-color: rgba(102, 126, 234, 0.3);
}

.prompt-category {
  font-size: 0.75rem;
  text-transform: uppercase;
  opacity: 0.6;
  font-weight: 600;
}

.prompt-text {
  font-size: 0.85rem;
  line-height: 1.3;
}

.prompt-button.opener .prompt-category { color: #667eea; }
.prompt-button.compliment .prompt-category { color: #ec4899; }
.prompt-button.question .prompt-category { color: #10b981; }
.prompt-button.story .prompt-category { color: #f59e0b; }
.prompt-button.flirt .prompt-category { color: #ef4444; }

.conversation-log {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 1.5rem;
  max-height: 300px;
  display: flex;
  flex-direction: column;
}

.conversation-log h3 {
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
}

.messages {
  flex: 1;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.message {
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 0.9rem;
  line-height: 1.4;
  animation: fadeIn 0.3s ease;
}

.message.user {
  background: rgba(102, 126, 234, 0.1);
  margin-left: 2rem;
}

.message.npc {
  background: rgba(236, 72, 153, 0.1);
  margin-right: 2rem;
}

.role {
  font-weight: 600;
  margin-right: 0.5rem;
}

.date-actions {
  display: flex;
  gap: 1rem;
}

.action-button {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.action-button.record {
  background: linear-gradient(135deg, #667eea 0%, #a855f7 100%);
  color: white;
}

.action-button.record:disabled {
  background: rgba(255, 255, 255, 0.1);
  opacity: 0.5;
}

.action-button.end {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
  border: 1px solid #ef4444;
}

.action-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

/* Responsive */
@media (max-width: 1024px) {
  .date-content {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
  
  .date-interface {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    gap: 1rem;
    overflow-y: visible;
  }
}
