/* Button styling - Updated for Risograph consistency */
button, .button, .btn {
  background-color: var(--riso-pink);
  color: var(--bg-light);
  border: 3px solid var(--text-dark);
  padding: var(--space-md) var(--space-xl);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-bold);
  font-family: var(--font-body);
  cursor: pointer;
  transition: all var(--transition-fast);
  border-radius: var(--radius-full);
  position: relative;
  box-shadow: 4px 4px 0 var(--text-dark);
  text-transform: none;
  letter-spacing: 0.01em;
}

button:hover, .button:hover, .btn:hover {
  transform: translate(-2px, -2px);
  box-shadow: 6px 6px 0 var(--text-dark);
  background-color: var(--riso-pink-light);
}

button:active, .button:active, .btn:active {
  transform: translate(0, 0);
  box-shadow: 2px 2px 0 var(--text-dark);
}

/* Secondary buttons */
.btn-secondary {
  background-color: var(--bg-light);
  color: var(--text-dark);
  border: 2px solid var(--text-dark);
  padding: var(--space-sm) var(--space-lg);
  box-shadow: 3px 3px 0 var(--riso-teal);
}

.btn-secondary:hover {
  background-color: var(--overlay-teal);
  box-shadow: 5px 5px 0 var(--riso-teal);
  transform: translate(-2px, -2px);
}

/* Primary emphasis button */
.btn-primary, .riso-button {
  background-color: var(--riso-goldenrod);
  color: var(--text-dark);
  border: 3px solid var(--text-dark);
  box-shadow: 4px 4px 0 var(--riso-indigo);
  font-size: var(--font-size-lg);
  padding: var(--space-md) var(--space-2xl);
}

.btn-primary:hover, .riso-button:hover {
  background-color: var(--riso-goldenrod-light);
  box-shadow: 6px 6px 0 var(--riso-indigo);
  transform: translate(-3px, -3px);
}

/* Form Control Styles (Inputs, Textareas) */
.form-control {
  display: block;
  width: calc(100% - 22px); /* Adjusts for padding and border to make it appear full-width */
  padding: var(--space-md);
  margin-bottom: var(--space-md);
  font-size: var(--font-size-base);
  line-height: var(--line-height-base);
  color: var(--text-dark);
  background-color: var(--bg-light);
  background-clip: padding-box;
  border: 1px solid var(--text-dark); /* Light grey border */
  border-radius: var(--radius-md); /* Moderately rounded corners */
  transition: border-color var(--transition-fast), box-shadow var(--transition-fast);
  box-sizing: border-box;
}

.form-control::placeholder {
  color: var(--text-medium); /* Softer placeholder text */
  opacity: 1;
}

.form-control:focus {
  color: var(--text-dark);
  background-color: var(--bg-light);
  border-color: var(--riso-pink); /* Coral border on focus */
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(255, 107, 157, 0.25); /* Coral glow using actual RGB values */
}

textarea.form-control {
  min-height: 100px;
  resize: vertical;
}

/* Video Player Styles */
.video-player {
  background-color: var(--text-dark); /* Black background for letterboxing or if video not loaded */
  border: 1px solid var(--text-dark);  /* Subtle border */
  border-radius: var(--radius-md);     /* Rounded corners */
  object-fit: cover;      /* Ensures video covers the dimensions, might crop */
  margin-bottom: var(--space-md);
  display: block; /* Ensures it behaves as a block element */
}

.video-player.video-off { /* Style for when video is off, used in VideoChat.js */
  background-color: var(--text-medium); /* Darker background when camera is off */
}